.navbar {
    background-color: transparent !important;
    min-height: 80px ;
}

.nav-link:focus {
    color: white !important;
}
.navbar-scrolled {
    background-color: rgb(10, 10, 10) !important;
}

.nav-icon {
    width: 22px;
    height: 22px;
    text-align: center;
    margin-left: 3px;  
    color: white;
    transition: 1s;
}
.back-icon{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border: 1px solid white;
    position: relative;
    transition: 1s;
}
.back-icon::after {
    position: absolute;
    content: "";
    width: 100%;  /* Define size */
    height: 100%; /* Define size */
    background-color: white;
    top: 0;
    left: 0;
    border-radius: 50%;
    transform: scale(0);
    transform-origin: center center;
    transition: 1s ;
    z-index: -1; 
}

.back-icon:hover::after {
    transform: scale(1);
}
.back-icon:hover .nav-icon {
    color: black;
}
.nav-btn {
    padding: 15px 30px;
    border: 1px solid white;
    color: white;
    font-weight: bold;
    position: relative;
    transition: 1s;
}
.nav-btn::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    transition: 1s;
    background-color: white;
    z-index: -1;
}
.nav-btn:hover::after{
    width: 100%;
    height: 100%;
}
.nav-btn:hover {
    color: black;
}