
.home-container {
    width: 100%;
    height: 100vh;
    position: relative;
}
.home-img{
    width: 100%;
    height: 100%;
}
.home-div1{   
    position: absolute;
    top: 30%;
    left: 5%;
    color: white;
}
.home-div2 {
    position: absolute;
    /* top: 35%; */
    left: 50%;
    animation-name: updown;
    animation-duration: 1.5s ;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}
@keyframes updown {
    0% {
        top: 30%;
    }
    100% {
        top: 25%;
    }
}
.home-head1 { 
    display: inline-block;
    padding: 10px 15px;
    border: 1px solid white;
    background: linear-gradient(to right, rgb(181, 185, 196) 30%, rgb(105, 106, 184));    
}
.home-head2{
    font-weight: 700;
    font-size: 3rem;
}
.home-p1 {
    color: rgb(150, 150, 150);
    font-weight: bold;
}
.home-s1 {
    font-weight: bold;
    display: inline-block;
    margin-right: 10px;
}
.home-div3 {
    margin-top: 10%;
    cursor: pointer;
}