.projects-content{
    text-align: center;
    color: white;
}
.proj-p1 {
    color: rgb(150, 150, 150);
    font-weight: bold;
}
.proj-d1{
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    border: 1px solid white;
}
.proj-d{
    padding: 20px 0px;
    background-color: rgb(16, 16, 16);
    cursor: pointer;
}
.proj-d3{
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border: 1px solid white;
}
.proj-d2 {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}
.proj-d:focus{
    background: linear-gradient(to right, rgb(181, 185, 196) 30%, rgb(105, 106, 184));    
}
.proj-container {
    margin: auto;
    font-weight: 600;
}
.grid-container {
    display: grid;
    width: 80%;
    margin: auto;
    grid-template-columns: repeat(3 , 1fr);
    gap: 30px;
    color: white;
}

.se1 img {
    width: 100%;
    height: 100%;
    border-radius: 30px;
}
.se1 {
    position: relative;
}

.se1::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    background: linear-gradient(to right, rgba(159, 169, 197, 0.9) 30%, rgba(53, 54, 105, 0.9));
    transition: 1s;
    border-radius: 30px;
}

.se1:hover::after {
    width: 100%;
    height: 100%;
}

.img-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
    opacity: 0;
    transition: 1s; 
}

.img-content h5 {
    font-weight: 700;
}

.se1:hover .img-content {
    opacity: 1;
}
.se2-container {
    background: linear-gradient(to right, rgb(181, 185, 196) 30%, rgb(105, 106, 184)); 
    height: 120vh;
}
.left-content img {
    width: 100%;
    /* height: 100%; */
}
.right-h1 {
    color: white;
    font-weight: 700;   
}
.form-btn {
    width: 130px;
    height: 50px;
    border: none;
    color: black;
    background-color: white;
    font-weight: bold;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
input , textarea {
    background-color: rgba(255, 255, 255, 0) !important;
    border-radius: 20px !important;
    padding: 15px !important;
}
input::placeholder , textarea::placeholder {
    color: white !important;
    opacity: 0.7 !important;
}
.se3-container {
    width: 40%;
    margin: auto;
    padding: 10px;
}
.bot-content {
    width: 70%;
    margin: auto;
    background-color: white;
    padding: 70px 80px;
    border-radius: 40px;
}
#email2::placeholder {
    color: rgba(0, 0, 0, 0.8) !important;
}
#email2 {
    border: 3px solid transparent; /* Set border to transparent */
    background: linear-gradient(white, white) padding-box, linear-gradient(to right, rgb(181, 185, 196) 30%, rgb(105, 106, 184)) border-box; /* Gradient on border */
    border-radius: 10px; /* Apply border-radius */
}
.right-bot {
    position: relative;
}
.pos-btn {
    position: absolute;
    left: 66%;
    border: none;
    background: linear-gradient(to right, rgb(181, 185, 196) 30%, rgb(105, 106, 184)); 
    padding: 15px 50px;
    border-radius: 10px;
    color: white;
}